import React, { useEffect, useState } from "react";
import { Snackbar, Alert, DialogContent, DialogActions, Dialog, Card, CardContent, Typography, CardActions, TextField, Button, Autocomplete } from "@mui/material";
import axios from "axios";
import { axiosInstance } from "./navbar/auth/token/Api";
import { numberFormat } from "./numberformat";
import './Style/media.css'

function BulkOut() {
    const [inputList, setInputList] = useState([{ Amount: "", Reason: "", itemPK: "" }]);
    const [item, setItem] = useState([]);
    const [snack, setSnack] = useState(false);
    const [snackText, setSnackText] = useState("");
    const [snackSeverity, setSnackSeverity] = useState("");
    const handleClose = () => {
        setSnack(false)
      };
    
    
    // handle input change
    const handleInputChange = (e, index) => {
        //console.log(e.target)
        const { name, value } = e.target;
        const list = [...inputList];
        list[index][name] = value;
        setInputList(list);
    };

    const handleAutoComplete = (e, index) => {
        const list = [...inputList];
        list[index]['itemPK'] = e;
        setInputList(list);
    };
                
    // handle click event of the Remove button
    const handleRemoveClick = index => {
        const list = [...inputList];
        list.splice(index, 1);
        setInputList(list);
    };    
    // handle click event of the Add button
    const handleAddClick = () => {
        setInputList([...inputList, { Amount: "", Reason: "", itemPK: "" }]);
        };
        const getCustomer = async () => {
            try {
                const { data } = await axiosInstance.get('bulk/all/item/')
                const item = data;
                setItem(item);
            } catch(error){
        }
    }
    const BulkOut = async (event) => {
        event.preventDefault();
        console.log(inputList)
        try {
            await axiosInstance.post('bulk/out/', {
                inputList: inputList,
            }).then(() => {
                setInputList([{ Amount: "", Reason: "", itemPK: "" }])
                setSnack(true)
                setSnackSeverity("success")
                setSnackText("Berhasil Mengurangi Stock Barang")
            })
        } catch(error){
            setSnack(true)
            setSnackSeverity("error")
            setSnackText("Gagal Mengurangi Stock Barang")
    }  
    }
    useEffect (() => {
        getCustomer();
}, []);
    const items = item.map(item => {return {'name': item.name + ' - ' + item.product_code  + ' - ' +  numberFormat(item.price ? item.price : 0), 'pk': item.pk}})
    const selected_item = inputList.map(x => {return x.itemPK})
    const item_filter =  item.filter(x => selected_item.includes(x.pk))
    const fix_item_filter = item_filter.map(x => {return {'name': x.name + ' - ' + x.product_code  + ' - ' +  numberFormat(x.price ? x.price : 0), 'pk': x.pk}})
    const fix_item_filter_pk = fix_item_filter.map(x => {return x.pk})
    const fixed_item_filter = items.filter(x => {return !fix_item_filter_pk.includes(x.pk)})
    //console.log(fix_item_filter_pk)
    //console.log(fixed_item_filter)
    return (
        <div className="divControl">
        <h1>Stock Keluar</h1>
        <p>Mengurangi Jumlah Stock Item Secara Bersamaan</p>
        <form onSubmit={BulkOut}>
        {inputList.map((x, i) => {
        return (
            <Card sx={{ minWidth: 275, marginBottom: 5 }}>
                <CardContent>

                <Autocomplete
                    name="itemPK"
                    onChange={(e, val) => handleAutoComplete(val.pk, i)}
                    //onChange={(e, val) => console.log(val)}
                    options={fixed_item_filter}
                    style={{ width: '100%' }}
                    disablePortal
                    id="combo-box-demo"
                    getOptionLabel={(option) => option.name}
                    getOptionValue={(option) => option.pk}
                    required
                    sx={{ width: 300 }}
                    renderInput={(params) =>  <TextField required fullwidth {...params} label="Pilih Item" />}
                />
                <br/>
                <TextField
                    fullWidth
                    required
                    name="Amount"
                    placeholder="Jumlah"
                    type='number'
                    value={x.Amount}
                    onChange={e => (handleInputChange(e, i), console.log(e.target))}    
                />
                <br/><br/>
                <TextField
                    fullWidth
                    required
                    name="Reason"
                    placeholder="Keterangan"
                    type='text'
                    multiline
                    rows={5}
                    value={x.Reason}
                    onChange={e => (handleInputChange(e, i), console.log(e.target))}    
                />
                </CardContent>
                <CardActions>
                    {inputList.length !== 1 && <Button
                    style = {{ color: 'red' }}
                    onClick={() => handleRemoveClick(i)}>Hapus</Button>}
                    {inputList.length - 1 === i && <Button onClick={handleAddClick}>Tambah</Button>}
                </CardActions>
            </Card>

        );
        })}
        
            <Button type="submit" style={{ float: 'right' }}>Submit</Button>
        </form>
        <div style={{ marginTop: 20, display: 'none' }}>{JSON.stringify(inputList)}</div>
        <br/><br/><br/>

        <Snackbar open={snack} autoHideDuration={6000} onClose={handleClose}>
            <Alert onClose={handleClose} severity={snackSeverity} sx={{ width: '100%' }}>
                {snackText}
            </Alert>
        </Snackbar>

    </div>
    );
}
    
export default BulkOut;
